import { motion } from "framer-motion"

const MotionPageHoc = (Component) => {
    return function HOC() {
        return (
            <motion.div
             initial = {{ 
                     opacity: 0,
                 }}
             animate = {{
                     opacity: 1,
                     transition: {
                         duration: 0.5,
                         type: "easeInOut"
                     }
                 }}
             exit = {{
                     opacity: 0,
                     transition: {
                         duration:0.5,
                         type: "spring",
                         ease: "easeInOut"
                     }
                 }}>
                <Component/>
            </motion.div>
        )
    }
}
export default MotionPageHoc