import { motion } from "framer-motion"
import React, {
    useState,
    useEffect
} from 'react'

const MotionHoc = (Component) => {
    
 
    return function HOC() {
        const [scrollX, setScrollX] = useState(false);
        console.log(scrollX);
        const scrollAmite = {
            opacity: 1,
            y:0,
            transition: {
                duration: 0.5,
                type: "easeInOut"
            }
           }
            const scrollXExit = {
                x: "-100%",
                transition: {
                    duration: 1,
                    type: "spring",
                    ease: "easeInOut"
                }
            }
           const scrollYExit = {
               y: "-100%",
               transition: {
                   duration: 1,
                   type: "spring",
                   ease: "easeInOut"
               }
           }
           function setScroll(flag) {
               setScrollX(flag)
           }
        return (
            <motion.div
             initial = {{opacity: 0,}}
             animate = {scrollAmite}
             exit = {scrollX?scrollXExit:scrollYExit} >
                <Component setScroll={setScroll}/>
            </motion.div>
        )
    }
}
export default MotionHoc